/* .App {
  text-align: center;
} */


.Main{
  position: absolute;
  background-color : #f4f3ef;
  display: grid;
  gap: 50px 150px;
  padding: 50px;
  grid:
  "dataload-label dataload" 0.2fr
  "dataview-label dataview" 0.3fr
  "selectplotview-label selectplotview" 0.3fr
  "selectdataview-label selectdataview" 0.3fr
  "modelview-label modelview" 0.3fr
  / 1fr 3fr;

  justify-content: space-around;
  align-content: space-between;
  justify-items: start;
  align-items: center;
}

[class $="label"]{
  font-size: 36px;
}

/* related data upload */
.dataload-label { grid-area: dataload-label; }
.dataload { grid-area: dataload; }
/* DataUpload - inputbox  */
.DataUpload{
  width: 74vw;
  height: 60px;
  line-height: 60px;
  border-width: 1px;
  border-style: dashed;
  border-radius: 5px;
  text-align: center;

  /* margin: 10px; */
}

/* related dataView  */
.dataview-label { grid-area: dataview-label; }
.dataview { 
  grid-area: dataview;
  background-color: white;
  width: 72vw;

  /* border-radius: 30px; */
    /* border-radius: top-left-x top-right-x bottom-right-x bottom-left-x / 
                    top-left-y top-right-y bottom-right-y bottom-left-y */

  /* 위 | 오른쪽 | 아래 | 왼쪽 */
  padding: 20px 20px 10px 20px;
}
 /* vw vh 는 뷰포는 너비와 높이에 맞게 조절됨 */
.dataview .table-root{
  overflow: auto;
  max-height: 40vh;
  margin-bottom: 10px;


}

/* th, table-root, table-row-color : tableArea  */
th {
  position: sticky;
  top: 0;
  background-color: white;
  color: #2b2b2a; 
}
th , td{
  border-bottom: 1px solid #212529;
  padding: 10px;
}
table{
  max-width: 100%;
  color : #212529;
  border-collapse: collapse;
  border-top: 1px solid #212529;

}
.pagination{
text-align: right;
}
.table .rt-thead{
  overflow-y: scroll;
}
.table-row-color{
  background: #35d1f2;
}

/* related select-plot-view */
.selectplotview-label { grid-area: selectplotview-label; }
.selectplotview { 
  grid-area: selectplotview;
  width: 78vw;


}

.selectPlot-xy-with{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  /* margin: 20px; */
}
.selectPlot-x-with{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 40px;

}
.selectPlot-x-with * {
  margin-top: 10px;
}
/* [속성^=값] 값으로 시작할 때 스타일 적용
    [속성~=값] 값이 정확히 포함될때 스타일 적용
*/


/* related selectedDataView */
.selectdataview-label { grid-area: selectdataview-label; }

.selectdataview { 
  grid-area: selectdataview; 
  background-color: white;
  width: 72vw;
  /* border-radius: 30px; */
  /* padding 위 | 오른쪽 | 아래 | 왼쪽 */
  padding: 20px 20px 10px 20px;
}

.selectdataview .table-root{
  overflow: auto;
  max-height: 25vh;
  margin-bottom: 10px;
}

/* related modelView  */
.modelview-label { grid-area: modelview-label; }
.modelview { grid-area: modelview; 
  width: 75vw;
}

.divModelResult{
  display: flex;
  gap: 20px;
  flex-direction: row;
  flex-wrap: wrap-reverse;
  justify-content: space-between;
}

.divModelExpression{
  font-size: 18px;
  width: 40vw;
}